import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './plugins/base'
import './plugins/chartist'
import './plugins/vee-validate'
import './plugins/vue-world-map'
import vuetify from './plugins/vuetify'
import i18n from './i18n'
import ego from './components/Utlis/http'
import Print from 'vue-print-nb'
import { findBestIp, findAccessRight } from './components/Utlis/api'
import GlobalSettings from './components/Settings/GlobalSettings'
import { IKUtils } from 'innerken-utils'
import './registerServiceWorker'

console.log(GlobalSettings.DefaultLocale)

GlobalSettings.DefaultLocale = IKUtils.getQueryString('lang')?.toLowerCase() ?? GlobalSettings.DefaultLocale

const Password = IKUtils.getQueryString('Password') ?? IKUtils.getQueryString('Password') ?? ''
store.commit('SET_PASSWORD', Password)

GlobalSettings.lang = function () {
  return i18n.locale.toUpperCase()
}

async function init () {
  const DeviceId = IKUtils.getQueryString('DeviceId') ?? -1
  const Base = IKUtils.getQueryString('Base') ?? false
  const initObject = { deviceId: -1, url: 'localhost' }
  initObject.deviceId = DeviceId
  if (parseInt(DeviceId) !== -1) {
    if (!Base) {
      const res = await findBestIp(DeviceId)
      initObject.url = res.content
    } else {
      initObject.url = Base
    }
  }
  store.commit('SAVE_CURRENT_DEVICE', initObject)
  Vue.config.productionTip = true
  ego.initial(store.getters.rootUrl + GlobalSettings.endPoint)
  Vue.use(Print)
  Vue.filter('priceDisplay',
    function (price) {
      return parseFloat(price).toFixed(2).replace('.', ',')
    },
  )

  let accessLevel = 1
  if (Password) {
    const accessArr = (await findAccessRight(Password)).content
    // 老板权限1， 跑堂0， 兼职-1
    if (accessArr.permission === '0') {
      accessLevel = accessArr.isPartTime === '0' ? 0 : -1
    }
  }
  store.commit('SET_ACCESS_LEVEL', accessLevel)
  new Vue({
    router,
    store,
    vuetify,
    i18n,
    render: h => h(App),
  }).$mount('#app')
}

init()
